.container {
    max-width: 1140px;
    margin: auto;
    margin-top: 50px;
}

.heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #313cdf;
    color: rgb(255, 255, 255);
    box-shadow: 0px 0px 12px #18191b;
    border-radius: 8px;
    margin: 2rem 1rem;
    padding: 1rem 1rem;
    font-weight: 700;
}

.coin-name{
    margin-left: -4rem;
}


img {
    height: 40px;
    margin-right: 8px;
    width: auto;
}

.img-symbol {
    display: flex;
    align-items: center;
}

.coin-row{
    display: flex;
    margin-left: -4rem;
    justify-content: space-between;
    align-items: center;
    background-color: #26272b;
    box-shadow: 0px 0px 12px #18191b;
    border-radius: 8px;
    margin: 2rem 1rem;
    padding: 1rem 1rem;
}

.coin-row:hover {
    transform:scale(1.04);
    transition: 0.3s ease-in-out;
    cursor: pointer;
}

@media screen and (max-width: 720px) {
    .hide-mobie {
        display: none;
    }
}